function App() {
 const [number, setUint] = useState(0);
 const [getNumber, setGet] = useState("0");

 const numberSet = async (t) => {
   t.preventDefault();
   const accounts = await window.ethereum.enable();
   const account = accounts[0];
   //const accounts = web3.eth.accounts;

  // window.ethereum
	//const accounts = await ethereum.request({ method: 'eth_accounts' };
  const gas = await rimmingContract.methods.set(number).estimateGas();
   const post = await rimmingContract.methods.set(number).send({
     from: account,
     gas,
   });
 };

 const numberGet = async (t) => {
   t.preventDefault();
   const post = await rimmingContract.methods.get().call();
   setGet(post);
 };
 
return (
   <div className="main">
     <div className="card">
       <form className="form" onSubmit={numberSet}>
         <label>
           Set your uint256:
           <input
             className="input"
             type="text"
             name="name"
             onChange={(t) => setUint(t.target.value)}
           />
         </label>
         <button className="button" type="submit" value="Confirm">
           Confirm
         </button>
       </form>
       <br />
       <button className="button" onClick={numberGet} type="button">
         Get your uint256
       </button>
       {getNumber}
     </div>
   </div>
 );
}

export default App;